/*end of about */
/*skills*/
.units{
  border:1px solid lightskyblue;
  border-radius: 3px;
  text-align:center;
  width:23vw;
  text-align: center;
  display:grid;
  
}
.skillsd{
  display:grid;
  grid-template-columns: auto auto auto;
}
@media (max-width:800px) {
  .units{
    width:100%;

  }
  .skillsd{
    grid-template-columns: auto;
  }
}

/*end of skills*/
